@mixin btn {
    height: 30px;
    width: 30px;
    background-color: blue;
    position: absolute;
    border: none;
    border-radius: 100%;
    background: linear-gradient(0deg, #CD25F7, #961BF9);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    cursor: pointer;
    outline: none;

    z-index: 20;
}
.person {
    display: flex;
    align-items: center;
    position: relative;

    &-delete {
        right: -60px;
        @include btn();
    }
    .photo {
        height: 60px;
        width: 60px;
        font-size: 36px;
        color: #D1D2D3;
        text-transform: uppercase;
        background-color: #F0EFF5;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 100%;
        margin-right: 20px;
    }

    &-wrap {
        display: flex;
        flex-direction: column;
    }

    &-name {
        font-size: 18px;
    }
    &-job {
        font-size: 18px;
        color: #ADB4BB;
    }
}
.next-person {
    position: relative;
    &_btn {
        top: 60px;
        left: -13px;
        transition: 0.2s all;
        @include btn();
        &:active {
            transform: scale(1.1);
        }
    }
    .line {
        border-left: 2px solid #98A1A9;
        width: 28px;
        height: 101px;
        border-bottom: 2px solid #98A1A9;
    }
}

.line-vertical {
    width: 2px;
    height: 60px;
    background-color: #98A1A9;
    position: absolute;
}
