
@mixin flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.input-data {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;

    &-text {
        color: #98A1A9;
        font-size: 14px;
        margin-bottom: 20px;
    }

    input {
        border: none;
        border-bottom: 1px solid #ccc;
        outline: none;
        width: 200px;
        font-size: 22px;
        height: 25px;
        &::placeholder {
            color: #757575;
            font-size: 22px;
        }
    }

    &-btn {
        text-transform: capitalize;
        height: 60px;
        width: 60px;
        font-size: 12px;
        border-radius: 100%;
        background: deepskyblue;
        color: #fff;
        @include flex-center;
        border: none;
        outline: none;
        transition: 0.2s all;
        &:hover {
            background-color: darken(deepskyblue, 30);
            cursor: pointer;
        }
        &:active {
            transform: translateY(3px);
        }

        &[disabled] {
            background-color: lighten(deepskyblue, 30);
        }
    }
}