.added_wrap {
    display: flex;
    align-items: center;
}

.add-person {
    height: 60px;
    width: 60px;
    background: linear-gradient(0deg, #CD25F7, #961BF9);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 20px;
    border: none;
    border-radius: 100%;
    margin-right: 20px;
    cursor: pointer;
    outline: none;
}

.text {
    font-size: 18px;
    font-weight: bold;
}